@-webkit-keyframes closeEyes {
  0% { top: -100%; }
  2% { top: 0%; }
  3% { top: -100% }
  100% { top: -100% }
}

@keyframes closeEyes {
  0% { top: -100%; }
  2% { top: 0%; }
  3% { top: -100% }
  100% { top: -100% }
}

@-webkit-keyframes moveEyes {
  0% { left: 25%; }
  5% { left: 10%; }
  10% { left: 25%; }
  50% { left: 25%; }
  55% { left: 40%; }
  60% { left: 25%; }
