  50% { left: 25%; }
  55% { left: 40%; }
  60% { left: 25%; }
  100% { left: 25%; }
}

@-webkit-keyframes steam {
  0% { -webkit-transform: rotateY(0deg) skewY(0deg); transform: rotateY(0deg) skewY(0deg); }
  25% { -webkit-transform: rotateY(90deg) skewY(30deg); transform: rotateY(90deg) skewY(30deg); }
  50% { -webkit-transform: rotateY(180deg) skewY(0deg); transform: rotateY(180deg) skewY(0deg); }
  75% { -webkit-transform: rotateY(270deg) skewY(-30deg); transform: rotateY(270deg) skewY(-30deg); }
  100% { -webkit-transform: rotateY(360deg) skewY(0deg); transform: rotateY(360deg) skewY(0deg); }
}

@keyframes steam {
  0% { -webkit-transform: rotateY(0deg) skewY(0deg); transform: rotateY(0deg) skewY(0deg); }
  25% { -webkit-transform: rotateY(90deg) skewY(30deg); transform: rotateY(90deg) skewY(30deg); }
  50% { -webkit-transform: rotateY(180deg) skewY(0deg); transform: rotateY(180deg) skewY(0deg); }
  75% { -webkit-transform: rotateY(270deg) skewY(-30deg); transform: rotateY(270deg) skewY(-30deg); }
  100% { -webkit-transform: rotateY(360deg) skewY(0deg); transform: rotateY(360deg) skewY(0deg); }
}


