.ContactsPage {
  height: 100%;
  width: 100%;
  text-align: center;
}

.light,
.light *,
.light *::-webkit-input-placeholder { color: #fff; }

.light,
.light *,
.light *:-ms-input-placeholder { color: #fff; }

.light,
.light *,
.light *::-ms-input-placeholder { color: #fff; }

.light,
.light *,
.light *::placeholder { color: #fff; }

.dark,
.dark *:not(button),
.dark *::-webkit-input-placeholder { color: #232323; }

.dark,
.dark *:not(button),
.dark *:-ms-input-placeholder { color: #232323; }

.dark,
.dark *:not(button),
.dark *::-ms-input-placeholder { color: #232323; }

.dark,
.dark *:not(button),
.dark *::placeholder { color: #232323; }

.ContactsPage__message {
  position: relative;
  z-index: 5;
  width: 90%;
  margin: 0 auto;
  font-size: 1.2rem;
}

.ContactsPage__form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  position: relative;
  margin: 0 auto;
  width: 90%;
  z-index: 5;
}

.ContactsPage__form__instruction {
  margin: 0;
  padding: 0;
  font-size: 1rem;
  text-align: left;
}

.letter {
  width:  100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 3rem;
  font-weight: bold;
  text-shadow: 0 2px #0973b1;
}

.day {
  color: #27adfa;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#30c2ee), to(#ff9735));
  background-image: -webkit-linear-gradient(#30c2ee, #ff9735);
  background-image: -o-linear-gradient(#30c2ee, #ff9735);
  background-image: linear-gradient(#30c2ee, #ff9735);
}

.night {
  color: #e9e9e9;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#170125), to(#360241));
  background-image: -webkit-linear-gradient(#170125, #360241);
  background-image: -o-linear-gradient(#170125, #360241);
  background-image: linear-gradient(#170125, #360241);
}

@media (min-width: 500px) and (min-height: 500px) {
  .ContactsPage__form { width: 70%; }
}

@media (min-height: 600px) {
  .ContactsPage__message { font-size: 1.5rem; }
}

@media (min-width: 700px) {
  .ContactsPage__message { width: 50%; }
  .ContactsPage__form { width: 50%; }
}


