.PageHeader {
  display: inline-block;
  position: relative;
  z-index: 10;
  margin: 2rem 0;
  font-size: 1.5rem;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: .2rem;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
  color: #006999; 
  text-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2); 
}

@media (min-width: 600px) {
  .PageHeader {
    font-size: 2rem; 
  } 
}
  
@media (min-width: 1000px) {
  .PageHeader {
    font-size: 2.5rem; 
  } 
}

.PageHeader:hover {
  -webkit-transform: skewY(2deg);
      -ms-transform: skewY(2deg);
          transform: skewY(2deg);
  text-shadow: 0.5rem 1rem 2rem rgba(0, 0, 0, 0.4); 
}


