.SkillIndex {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 50%;
  margin: 0 auto 1rem;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
}

@media (min-width: 500px) {
  .SkillIndex { width: 30%; }
}

@media (min-width: 768px) {
  .SkillIndex { width: 25%; }
