.indicator {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  width: 90%;
  margin: 0 auto;
  border-radius: 1rem;
  -webkit-box-shadow: 0 .1rem .25rem rgba(0,0,0,.3) inset, 0 -0.1rem .1rem rgba(255,255,255,.1) inset;
          box-shadow: 0 .1rem .25rem rgba(0,0,0,.3) inset, 0 -0.1rem .1rem rgba(255,255,255,.1) inset;
  background-color: rgba(0,0,0,.2);
}

.indicator::before {
  position: absolute;
  content: "";
  top: 2.5rem;
  left: 25%;
  width: 50%;
  height: 5rem;
  background-image: -webkit-gradient(
    linear, left top, left bottom, color-stop(20%, #4e4e4e), color-stop(20%, transparent), to(transparent)
  );
  background-image: -webkit-linear-gradient(
    #4e4e4e 20%, transparent 20%, transparent 100%
  );
  background-image: -o-linear-gradient(
    #4e4e4e 20%, transparent 20%, transparent 100%
  );
  background-image: linear-gradient(
    #4e4e4e 20%, transparent 20%, transparent 100%
  );
  background-size:  100% 10%;
}

.progress {
  position: relative;
  height: 6rem;
  margin: 2rem 0;
  width: 5%;
  background-color: #1f1f1f;
  border-radius: .5rem;
  background-image: -webkit-gradient(linear, left top, right top, from(#141414), color-stop(transparent), to(#141414));
  background-image: -webkit-linear-gradient(left, #141414, transparent, #141414);
  background-image: -o-linear-gradient(left, #141414, transparent, #141414);
  background-image: linear-gradient(90deg, #141414, transparent, #141414);
  -webkit-box-shadow: 0 .2rem .25rem rgba(0,0,0,.6) inset, 0 -.1rem .1rem rgba(255,255,255,.4) inset;
          box-shadow: 0 .2rem .25rem rgba(0,0,0,.6) inset, 0 -.1rem .1rem rgba(255,255,255,.4) inset;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
}

.progressValue {
  position: absolute;
  bottom:  0;
  width: 100%;
}

.progressBar {
  position: absolute;
  top: 100%;
  left: calc(-2rem + 50%);
  width: 4rem;
  height: 2rem;
  background-image: -webkit-gradient(linear, left top, right top, color-stop(5%, #141414), color-stop(5%, transparent), color-stop(95%, transparent), color-stop(95%, #141414)),
    -webkit-gradient(linear, left top, left bottom, from(#727272), color-stop(45%, #303030), color-stop(45%, #d1d1d1), color-stop(55%, #d1d1d1), color-stop(55%, #141414), to(#111));
  background-image: -webkit-linear-gradient(left, #141414 5%, transparent 5%, transparent 95%, #141414 95%),
    -webkit-linear-gradient(#727272, #303030 45%, #d1d1d1 45%, #d1d1d1 55%, #141414 55%, #111);
  background-image: -o-linear-gradient(left, #141414 5%, transparent 5%, transparent 95%, #141414 95%),
    -o-linear-gradient(#727272, #303030 45%, #d1d1d1 45%, #d1d1d1 55%, #141414 55%, #111);
  background-image: linear-gradient(90deg, #141414 5%, transparent 5%, transparent 95%, #141414 95%),
    linear-gradient(#727272, #303030 45%, #d1d1d1 45%, #d1d1d1 55%, #141414 55%, #111);
  -webkit-box-shadow: 0 .5rem .5rem rgba(0,0,0,.4);
          box-shadow: 0 .5rem .5rem rgba(0,0,0,.4);
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  -webkit-animation: loading 1s forwards;
          animation: loading 1s forwards;
}

.indicator:hover .progress {
  background-color: #02ff89;
}

@-webkit-keyframes loading {
  from { top: 100%; }
  to { top: 0; }
}

@keyframes loading {
  from { top: 100%; }
  to { top: 0; }
}


