.SocialMedia {
  width: 100%;
  height: 10vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 2rem;
  color: white;
  font-size: 1.25rem;
}

.SocialMedia ul {
  list-style: none;
  display: -ms-flexbox;
  display: flex;
}

.SocialMedia a {
  display: inline-block;
  margin-left: 2rem;
  font-size: 2rem;
  color: inherit;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  -webkit-animation: appearFromBottom .75s ease-out backwards;
          animation: appearFromBottom .75s ease-out backwards;
}

.SocialMedia a:hover {
  color: #f38406;
}

@media (min-height: 500px) {
  .SocialMedia a {
    font-size: 3rem;
  }
}

@-webkit-keyframes appearFromBottom {
  0% {
    opacity: 0;
    -webkit-transform: translateY(3rem);
            transform: translateY(3rem);
  }
  80% {
    -webkit-transform: translateY(-.5rem);
            transform: translateY(-.5rem);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes appearFromBottom {
  0% {
    opacity: 0;
    -webkit-transform: translateY(3rem);
            transform: translateY(3rem);
  }
  80% {
    -webkit-transform: translateY(-.5rem);
            transform: translateY(-.5rem);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}



