.Project {
  width: 100%;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.Project__img {
  width: 100%;
  -webkit-transition: all 3s;
  -o-transition: all 3s;
  transition: all 3s;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

.Project__title {
  width: 100%;
  position: absolute;
  top: -50%;
  left: 0;
  color: #27adfa;
  text-shadow: 0 2px #0973b1;
  text-align: center;
  font-weight: bold;
  font-size: 2rem;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}

.Project:hover .Project__title {
  top: 50%;
}

@media (min-width: 450px) {
  .Project { width: 50%; }
}

@media (min-width: 768px) {  
  .Project__img { -webkit-transform: scale(1.15); -ms-transform: scale(1.15); transform: scale(1.15); }
  .Project:hover .Project__img { -webkit-transform: scale(1.02); -ms-transform: scale(1.02); transform: scale(1.02); }
}

.fromTop { -webkit-animation: appearFromTop 1.5s 1s backwards; animation: appearFromTop 1.5s 1s backwards; }
.fromBottom { -webkit-animation: appearFromBottom 1.5s 1s backwards; animation: appearFromBottom 1.5s 1s backwards; }
.fromLeft { -webkit-animation: appearFromLeft 1.5s 1s backwards; animation: appearFromLeft 1.5s 1s backwards; }
.fromRight { -webkit-animation: appearFromRight 1.5s 1s backwards; animation: appearFromRight 1.5s 1s backwards; }

@-webkit-keyframes appearFromTop {
  from { -webkit-transform: translateY(-100%); transform: translateY(-100%); opacity: 0; }
  to { -webkit-transform: translateY(0); transform: translateY(0); opacity: 1; }
}

@keyframes appearFromTop {
  from { -webkit-transform: translateY(-100%); transform: translateY(-100%); opacity: 0; }
  to { -webkit-transform: translateY(0); transform: translateY(0); opacity: 1; }
}

@-webkit-keyframes appearFromBottom {
  from { -webkit-transform: translateY(100%); transform: translateY(100%); opacity: 0; }
  to { -webkit-transform: translateY(0); transform: translateY(0); opacity: 1; }
}

@keyframes appearFromBottom {
  from { -webkit-transform: translateY(100%); transform: translateY(100%); opacity: 0; }
  to { -webkit-transform: translateY(0); transform: translateY(0); opacity: 1; }
}

@-webkit-keyframes appearFromLeft {
  from { -webkit-transform: translateX(-100%); transform: translateX(-100%); opacity: 0; }
  to { -webkit-transform: translateX(0); transform: translateX(0); opacity: 1; }
}

@keyframes appearFromLeft {
  from { -webkit-transform: translateX(-100%); transform: translateX(-100%); opacity: 0; }
  to { -webkit-transform: translateX(0); transform: translateX(0); opacity: 1; }
}

@-webkit-keyframes appearFromRight {
  from { -webkit-transform: translateX(100%); transform: translateX(100%); opacity: 0; }
  to { -webkit-transform: translateX(0); transform: translateX(0); opacity: 1; }
}

@keyframes appearFromRight {
  from { -webkit-transform: translateX(100%); transform: translateX(100%); opacity: 0; }
  to { -webkit-transform: translateX(0); transform: translateX(0); opacity: 1; }
}


