.Logo {
  position: relative;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  width: 4rem;
  color: #fff;
  font-family: 'Lalezar', cursive;
}

.Logo__firstname {
  font-size: 2rem;
  /* animation: appearFromTop 1s .8s backwards; */
}

.Logo__lastname {
  font-size: 1rem;
  -webkit-animation: appearFromLeft 1.3s backwards;
          animation: appearFromLeft 1.3s backwards;
}

.letter {
  -webkit-animation: type .2s backwards;
          animation: type .2s backwards;
}

@media (min-height: 500px) {
  .Logo__firstname { font-size: 2.5rem; }
  .Logo__lastname { font-size: 1.5rem; }
}

@-webkit-keyframes type {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes type {
  from { opacity: 0; }
  to { opacity: 1; }
}

@-webkit-keyframes appearFromLeft {
  from { -webkit-transform: translateX(-200%); transform: translateX(-200%); }
  to { -webkit-transform: translateX(0); transform: translateX(0); }
}

@keyframes appearFromLeft {
  from { -webkit-transform: translateX(-200%); transform: translateX(-200%); }
  to { -webkit-transform: translateX(0); transform: translateX(0); }
}


