.CityImage {
  position: absolute;
  bottom: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
}

.house {
  position: relative;
  background-color: #232323;
}

.aerial {
  position: absolute;
  background-color: #232323;
}

.night { background-color: #050505; }

.flat {
  display: inline-block;
  border: 1px solid #050505;
}

.signal {
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 1rem;
  height: 1rem;
  border-radius: 100%;
  background-image: none;
  -webkit-animation: blink 1.5s infinite;
          animation: blink 1.5s infinite;
}

@-webkit-keyframes blink {
  0% { background-image: -webkit-radial-gradient(#ca1313 20%, transparent); background-image: radial-gradient(#ca1313 20%, transparent); }
  25% { background-image: none; }
  100% { background-image: none; }
}

@keyframes blink {
  0% { background-image: -webkit-radial-gradient(#ca1313 20%, transparent); background-image: -o-radial-gradient(#ca1313 20%, transparent); background-image: radial-gradient(#ca1313 20%, transparent); }
  25% { background-image: none; }
  100% { background-image: none; }
}


