.NavigationMenuItem {
  text-transform: uppercase;
  font-weight: thin;
  font-size: 1.5rem;
  margin: 1rem auto;
}

@media (min-width: 1000px) {
  .NavigationMenuItem {
    margin: 0 2rem;
  }
}

.nav__button,
.nav__button:visited {
  display: inline-block;
  color: white;
  padding: 1rem;
  text-decoration: none;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
}

.nav__button:hover {
  color: #27adfa;
}

.active {
  color: white;
  background-color: #27adfa;
  -webkit-box-shadow: 0 1rem 1.5rem rgba(0,0,0,.4);
          box-shadow: 0 1rem 1.5rem rgba(0,0,0,.4);
}

.active:hover{
  color: white;
  -webkit-transform: rotate(3deg) scale(1.1);
      -ms-transform: rotate(3deg) scale(1.1);
          transform: rotate(3deg) scale(1.1);
  background-color: #1093eb;
  -webkit-box-shadow: 0 1.5rem 2.5rem rgba(0,0,0,.3);
          box-shadow: 0 1.5rem 2.5rem rgba(0,0,0,.3);
}
.active:active {
  -webkit-transform: rotate(3deg) scale(1);
      -ms-transform: rotate(3deg) scale(1);
          transform: rotate(3deg) scale(1);
  background-color: #0973b1;
  -webkit-box-shadow: 0 1rem 1.5rem rgba(0,0,0,.4);
          box-shadow: 0 1rem 1.5rem rgba(0,0,0,.4);
}


