.DrawerToggle {
    width: 40px;
    height: 40px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    padding: .5rem 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    cursor: pointer;
}

.DrawerToggle div {
    width: 90%;
    height: 2px;
    background-color: white;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
}

.open div:first-of-type{
    -webkit-transform: translateY(10px) rotate(140deg);
        -ms-transform: translateY(10px) rotate(140deg);
            transform: translateY(10px) rotate(140deg);
}

.open div:nth-of-type(2){
    -webkit-transform: translateX(15px);
        -ms-transform: translateX(15px);
            transform: translateX(15px);
    opacity: 0;
}

.open div:nth-of-type(3){
    -webkit-transform: translateY(-10px) rotate(-140deg);
        -ms-transform: translateY(-10px) rotate(-140deg);
            transform: translateY(-10px) rotate(-140deg);
}

.close:hover div:first-of-type{
    -webkit-transform: translateY(-3px);
        -ms-transform: translateY(-3px);
            transform: translateY(-3px);
}


.close:hover div:nth-of-type(3){
    -webkit-transform: translateY(3px);
        -ms-transform: translateY(3px);
            transform: translateY(3px);
}

@media (min-width: 1000px) {
    .DrawerToggle {
        display: none;
    }
}


