.RoomImage {
  position: relative;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.wall {
  position: absolute;
  top: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 100%;
  height: 100%;
}

.wall__side,
.wall__center_top,
.wall__center_bottom {
  background-color: #9c9c9c;
  background-image: 
    -webkit-linear-gradient(115deg, #bbbbbb 23px, transparent 23px),
    -webkit-linear-gradient(295deg, #c4c4c4 23px, transparent 23px),
    -webkit-linear-gradient(115deg, #bbbbbb 23px, transparent 23px),
    -webkit-linear-gradient(295deg, #c4c4c4 23px, transparent 23px);
  background-image: 
    -o-linear-gradient(115deg, #bbbbbb 23px, transparent 23px),
    -o-linear-gradient(295deg, #c4c4c4 23px, transparent 23px),
    -o-linear-gradient(115deg, #bbbbbb 23px, transparent 23px),
    -o-linear-gradient(295deg, #c4c4c4 23px, transparent 23px);
  background-image: 
    linear-gradient(335deg, #bbbbbb 23px, transparent 23px),
    linear-gradient(155deg, #c4c4c4 23px, transparent 23px),
    linear-gradient(335deg, #bbbbbb 23px, transparent 23px),
    linear-gradient(155deg, #c4c4c4 23px, transparent 23px);
  background-size: 58px 58px;	
  background-position: 0px 2px, 4px 35px, 29px 31px, 34px 6px;
}

.wall__side {
  width: 5%;
  height: 100%;
}

.wall__center {
  display: -ms-flexbox;
  display: flex;
  width: 90%;
  height: 100%;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.wall__center_top {
  width: 100%;
  height: 5%;
}

.wall__center_bottom {
  width: 100%;
  height: 15%;
}

.windows {
  width: 100%;
  height: 80%;
  display: -ms-flexbox;
  display: flex;
  border: 2rem solid #d1d1d1;
}

.windows__window {
  width: 25%;
  height: 100%;
  border: 1rem solid #e2e2e2;
  -webkit-perspective: 1500px;
          perspective: 1500px;
  -webkit-perspective-origin: bottom;
          perspective-origin: bottom;
}

.windows__window:last-of-type { cursor: pointer; }

.window__border {
  height: 100%;
  width: 100%;
  border: 1.5rem solid #e2e2e2;
  -webkit-box-shadow: -2px -2px 2px rgba(0,0,0,.2), 2px 2px 2px rgba(0,0,0,.2);
          box-shadow: -2px -2px 2px rgba(0,0,0,.2), 2px 2px 2px rgba(0,0,0,.2);
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
}

.window__grip {
  position: absolute;
  bottom: 1%;
  left: 30%;
  width: 30%;
  height: 2%;
  border-radius: 0 20% 20% 0;
  background-image: -webkit-gradient(linear, left top, right top, color-stop(5%, #aaa), color-stop(8%, transparent), to(#aaa)), 
    -webkit-gradient(linear, left top, left bottom, from(#aaa), color-stop(#fff), to(#aaa));
  background-image: -webkit-linear-gradient(left, #aaa 5%, transparent 8%, #aaa), 
    -webkit-linear-gradient(#aaa, #fff, #aaa);
  background-image: -o-linear-gradient(left, #aaa 5%, transparent 8%, #aaa), 
    -o-linear-gradient(#aaa, #fff, #aaa);
  background-image: linear-gradient(90deg, #aaa 5%, transparent 8%, #aaa), 
    linear-gradient(#aaa, #fff, #aaa);
  -webkit-box-shadow: 2px 2px .5rem rgba(0,0,0,.3);
          box-shadow: 2px 2px .5rem rgba(0,0,0,.3);
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  -webkit-transform-origin: right;
      -ms-transform-origin: right;
          transform-origin: right;
}

.window__glass {
  height: 100%;
  width: 100%;
  background-image: -webkit-linear-gradient(45deg, 
    transparent 60%, rgba(255,255,255,.2) 60%, 
    rgba(255,255,255,.2) 80%, transparent 80%);
  background-image: -o-linear-gradient(45deg, 
    transparent 60%, rgba(255,255,255,.2) 60%, 
    rgba(255,255,255,.2) 80%, transparent 80%);
  background-image: linear-gradient(45deg, 
    transparent 60%, rgba(255,255,255,.2) 60%, 
    rgba(255,255,255,.2) 80%, transparent 80%);
  -webkit-box-shadow: 2px 2px #7a7a7a inset;
          box-shadow: 2px 2px #7a7a7a inset;
}

.opened {
  -webkit-transform: rotateX(-50deg);
          transform: rotateX(-50deg);
}

.opened .window__grip {
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
  bottom: -3%;
}



