.Button,
.Button:visited {
  position: relative;
  display: inline-block;
  text-transform: uppercase;
  text-decoration: none;
  font-family: inherit;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
  border: none;
  cursor: pointer;
  color: white;
  text-shadow: none;
  -webkit-animation: appearFromBottom .75s .75s ease-out backwards;
          animation: appearFromBottom .75s .75s ease-out backwards;
}

.large { 
  padding: 1.5rem 4rem; 
  font-size: 1.5rem; 
  font-weight: bold; 
  -webkit-box-shadow: 0 1rem 1.5rem rgba(0, 0, 0, .2); 
          box-shadow: 0 1rem 1.5rem rgba(0, 0, 0, .2);
}

.medium { 
  padding: 1rem 2rem; 
  font-size: 1.2rem; 
  font-weight: bold; 
  -webkit-box-shadow: 0 .5rem 1rem rgba(0, 0, 0, .3); 
          box-shadow: 0 .5rem 1rem rgba(0, 0, 0, .3);
}

.small { 
  padding: .5rem 1rem; 
  font-size: 1rem; 
  font-weight: 300; 
  -webkit-box-shadow: 0 .25rem .5rem rgba(0, 0, 0, .4); 
          box-shadow: 0 .25rem .5rem rgba(0, 0, 0, .4);
}

.Button:hover {
  -webkit-transform: translateY(-0.3rem);
      -ms-transform: translateY(-0.3rem);
          transform: translateY(-0.3rem);
}

.Button:active {
  outline: none;
  -webkit-transform: translateY(-0.1rem);
      -ms-transform: translateY(-0.1rem);
          transform: translateY(-0.1rem);
  -webkit-box-shadow: 0 .5rem .5rem rgba(0, 0, 0, .5);
          box-shadow: 0 .5rem .5rem rgba(0, 0, 0, .5);
}

.Button:focus { outline: none; }

.blue,
.blue:visited { background-color: #27adfa; }
.blue:hover { background-color: #1093eb; }
.blue:active { background-color: #0973b1; }

.orange,
.orange:visited { background-color: #f38406; }
.orange:hover { background-color: #ff5c10; }
.orange:active { background-color: #b62a00; }

@-webkit-keyframes appearFromBottom {
  0% {
    opacity: 0;
    -webkit-transform: translateY(3rem);
            transform: translateY(3rem);
  }
  80% {
    -webkit-transform: translateY(-.5rem);
            transform: translateY(-.5rem);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes appearFromBottom {
  0% {
    opacity: 0;
    -webkit-transform: translateY(3rem);
            transform: translateY(3rem);
  }
  80% {
    -webkit-transform: translateY(-.5rem);
            transform: translateY(-.5rem);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}


